<template>
  <div>
    <div class="tab-display">
    <div v-for="(item,index) in list" :class="{'color-y':item == url}" @click="onNavigate(item)" :key="item">{{item}}<span @click="onDelete(index)">×</span></div>
    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default {
    data (){
      return {

      }
    },
    computed:{
      // list(){
      //   return this.$store.getters.getTab
      // }
      ...mapGetters({
        url:'getUrl',
        list:'getTab'
      })
    },
    updated(val){
      console.log('ooooo',val)
    },
    methods:{
      onDelete(index){
        this.list.splice(index,1)
      },
      onNavigate(url){
        this.$store.commit('menu',url)
      }
    }
  }
</script>
<style scoped>
  .tab-display{
    display: flex;
  }
</style>



