<template>
  <div>
    <el-row class="tac">
      <el-col :span="12">
        <el-menu
          :default-active="indexMode"
          class="el-menu-vertical-demo"
          background-color="rgba(37, 44, 57, 1)"
          text-color="#fff"
          @open="handleOpen"
          @close="handleClose"
        @select="selectIndex">
          <el-submenu :index="item.name+''" v-for="(item,index) in menuList" :key="item">
              <template slot="title">
                <i class="el-icon-location"></i>
                <span>{{item.name}}</span>
              </template>


              <el-menu-item-group>
                <el-menu-item :index="mode+''" v-for="(mode,index) in item.one" :key="index">
                  <span v-if="mode">
                    {{mode}}
                  </span>
                </el-menu-item>
              </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-col>
    </el-row>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        indexMode:'',
        menuList:[{name:'员工自助',one:['rth','sdfh']},{name:'人才管理',one:['fileIndicatorDisk','fileReview']}]
      }
    },
    methods:{
      selectIndex(key,index){
        this.indexMode = key
        this.$store.commit('menu',key)
        this.$store.commit('tabList',[key])
      },
      handleOpen(key, keyPath) {
        console.log('handleOpen',key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log('handleClose',key, keyPath);
      }
    }
  }
</script>
<style scoped>
  .el-menu-vertical-demo{
    width: 100%;
  }
  .el-col-12{
    width:100%;
  }
</style>



