<template>
  <div>
    <div ref="main" style="width: 300px;height:300px;"></div>
  </div>
</template>
<script>
  export default {
    data (){
      return {
        modelist:[

          {value:38, name:'66%', itemStyle: {
              color: '#000'
            }},
          {value:66, name:'38%', itemStyle: {
              color: '#f00'      //设置扇叶单独颜色
            }}

        ]
      }
    },
    mounted(){

      // 基于准备好的dom，初始化echarts实例
      var myChart = this.$echarts.init(this.$refs.main);
      console.log('echart',myChart)
      // console.log('echart---------',myCharttt)
      myChart.setOption({
        backgroundColor: '#fff',  //背景色
        textStyle: {  //样式修改
          color: '#f00',  //字体颜色
          // fontWeight:100,
          fontSize:18
        },

        labelLine: {
          lineStyle: {
            color: '#f00'  //视觉引导线颜色
          }
        },
        legend: {
          // x: 'right',  //x , y 调节上下左右
          //y:'top',
          type: 'scroll',
          orient: 'vertical',
          right: 10,    //  调节标注位置
          top: 20,
          bottom: 20,
          // data:['66%','38%']
        },
        series : [
          {
            name:'你好',
            type: 'pie',
            radius: '30%',      //图的大小
            center:['50%','50%'],  //中心到父标签边框距离
            data:this.modelist,
            itemStyle: {  //鼠标移上阴影
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      })
    }
  }
</script>



