<template>
  <div>
    <keep-alive>
       <component :is="name"></component>
    </keep-alive>
  </div>
</template>
<script>
  import fileIndicatorDisk from './fileManagement/fileIndicatorDisk.vue'
  import fileReview from './fileManagement/fileReview.vue'
    export default {
    components: {
      fileIndicatorDisk,
      fileReview
    },
      data () {
        return {

        }
      },
      computed:{
        name:function (){
          return this.$store.getters.getName
        }
      },
      mounted (){

      }
    }
</script>



